  0% {

    -o-transform: scale(1.2);

    opacity: 1

  }

  100% {

    -o-transform: scale(0.7);

    opacity: 0.1

  }

}



@keyframes bounceG {

  0% {

    transform: scale(1.2);

    opacity: 1

  }

  100% {

    transform: scale(0.7);

    opacity: 0.1

  }

}





